import React, { useState } from 'react';
import './ReportPage.css';

const ReportPage: React.FC = () => {
  const [location, setLocation] = useState({ latitude: '', longitude: '' });
  const [file, setFile] = useState<File | null>(null);
  const [reportType, setReportType] = useState('');
  const [otherDescription, setOtherDescription] = useState('');

  const handleGetLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((position) => {
        setLocation({
          latitude: position.coords.latitude.toString(),
          longitude: position.coords.longitude.toString(),
        });
      });
    } else {
      alert('위치 정보를 가져올 수 없습니다.');
    }
  };

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files && event.target.files.length > 0) {
      setFile(event.target.files[0]);
    }
  };

  const handleReportTypeChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    setReportType(event.target.value);
  };

  const handleOtherDescriptionChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setOtherDescription(event.target.value);
  };

  const handleSubmit = () => {
    // 신고 제출 로직 추가
    console.log('신고 내용:', { location, file, reportType, otherDescription });
  };

  return (
    <div className="report-container">
      <h1>차량 신고</h1>
      <button className="get-location-btn" onClick={handleGetLocation}>
        위치 가져오기
      </button>
      <p>
        <strong>위치 정보:</strong> 위도: {location.latitude}, 경도: {location.longitude}
      </p>

      <div className="section">
        <h2>사진 및 동영상 등록</h2>
        <input type="file" onChange={handleFileChange} />
        {file && <p>선택된 파일: {file.name}</p>}
      </div>

      <div className="section">
        <h2>신고 내용</h2>
        <select value={reportType} onChange={handleReportTypeChange}>
          <option value="">신고 내용을 선택해 주세요.</option>
          <option value="사고">사고</option>
          <option value="과속">과속</option>
          <option value="신호 위반">신호 위반</option>
          <option value="주차 금지">주차 금지</option>
          <option value="기타">기타</option>
        </select>
        {reportType === '기타' && (
          <input
            type="text"
            placeholder="기타 내용을 입력해 주세요."
            value={otherDescription}
            onChange={handleOtherDescriptionChange}
          />
        )}
      </div>

      <button className="submit-btn" onClick={handleSubmit}>
        신고하기
      </button>
    </div>
  );
};

export default ReportPage;